// app/case-studies/[id]/page.tsx
import { caseStudiesData } from "../data";
import { notFound } from "next/navigation";
import { Star, Check, ArrowRight, Phone } from "lucide-react";
import Link from "next/link";

export default function CaseStudyDetail({ params }: { params: { id: string } }) {
  // Find the specific case study by the ID in the URL
  const project = caseStudiesData.find((p) => p.id === params.id);

  if (!project) return notFound(); // Shows 404 if project doesn't exist

  return (
    <main className="bg-white">
      {/* 1. HERO - Data is injected here */}
      <section className="w-full pt-32 pb-20 bg-slate-50 flex justify-center border-b border-slate-100">
        <div className="w-[85%] max-w-7xl">
          <span className="text-blue-600 font-bold text-xs uppercase tracking-[0.3em] mb-6 block">{project.category}</span>
          <h1 className="text-4xl md:text-6xl font-bold text-slate-900 mb-8 max-w-4xl tracking-tight leading-tight">
            {project.title}
          </h1>
          <p className="font-medium text-slate-500">Industry: <span className="text-slate-900">{project.industry}</span></p>
        </div>
      </section>

      {/* 2. STATS GRID - Dynamically mapped */}
      <section className="w-full flex justify-center py-24">
        <div className="w-[85%] max-w-7xl grid grid-cols-2 lg:grid-cols-4 gap-8">
          {project.stats.map((stat, i) => (
            <div key={i} className="border-t-2 border-blue-600 pt-6">
              <p className="text-4xl font-black text-slate-900 mb-2">{stat.val}</p>
              <p className="text-sm text-slate-500 font-medium">{stat.label}</p>
            </div>
          ))}
        </div>
      </section>

      {/* ... Rest of the template we built (Challenge, Solution, Results, Tech Stack) ... */}
      {/* Simply replace the hardcoded text with project.challenge, project.solution, etc. */}
      
      {/* 5. TECH STACK - Dynamically mapped */}
      <div className="flex flex-wrap gap-3">
        {project.techStack.map((tech) => (
          <span key={tech} className="px-6 py-3 bg-slate-100 rounded-xl text-slate-900 font-bold text-sm">
            {tech}
          </span>
        ))}
      </div>
    </main>
  );
}